@import "https://fonts.googleapis.com/css?family=Share+Tech+Mono";
@import url('https://fonts.googleapis.com/css2?family=Sedgwick+Ave+Display&display=swap');
@font-face {
  font-family: "Digital";
  src: url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot");
  src: url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot?#iefix")
      format("embedded-opentype"),
    url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff2")
      format("woff2"),
    url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff")
      format("woff"),
    url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.ttf")
      format("truetype"),
    url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.svg#Digital-7")
      format("svg");
}


* {
  box-sizing: border-box;
}

body {
  background-image: url('https://i.imgur.com/gjZW4Jm.jpg');
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #341f97;
  color: white;
  font-family: 'Sedgwick Ave Display', cursive;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  text-align: center;
}

button {
  background-color: transparent;
  color: white;
  font-size: 20px;
  padding: 10px 15px;
  margin: 10px;
  border: none;
  outline:1px solid white;
  cursor: pointer;
  }

.button-time {
    background-color: transparent;
    color: white;
    font-size: 20px;
    padding: 10px 15px;
    margin: 10px;
    color: #333;
    border: none;
    outline:1px solid #333;
    cursor: pointer;
    }

.actions-wrapper span {
  font-family: digital;
  font-size: 40px;
  margin: 0 10px;
  width: 50px;
}

.flex {
  display: flex;
  align-items: center;
  justify-content: center;
}

.timer-container {
  margin: -50px 20px 20px;
}

.timer-container h2{
  text-align: center;
  margin: 0;
  font-size: 32px;
}

.clock-container {
  background-color: rgba(255, 255, 255, 0.85);
  border: 2px solid white;
  border-radius: 10px;
  color: #333;
  text-align: center;
  padding: 10px;
  width: 380px;
  display: inline-block;
  margin-top: 20px;
}

@supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) {
  .clock-container {
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    background-color: rgba(255, 255, 255, 0.5);  
  }

}



.clock-container h1 {
  margin: 0;
  font-size: 48px;
}

.clock-container span {
  font-family: digital;
  font-size: 60px;
}